.listContainer {
    width: 100%;
    max-width: 500px;
    margin: 20px 0 20px 0;
}

.iconBtn {
    outline: none;
    margin: 0;
    border: none;
    background-color: transparent;
}

.listTitle {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    
}

.listItem {
    display: flex;
    align-items: center;
    word-break: break-word;
    min-height: 35px;
    justify-content: space-between;
    margin-bottom: 5px;
    padding-left: 10px;
    padding-right: 5px;
    background-color: white;
}

.listItem p {
    max-width: 90%;
}

.newItemContainer {
    display: flex;
    justify-content: space-between;

}

.newItemContainer button {
    border: none;
    width: 125px;
    background-color: white;
}

.newItemContainer input {
    margin: 0;
    border: 0;
    border-radius: 0;
    height: 35px;
}